:root {
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
}

.card__infos {
  margin-top: 40px;
}

.card__name {
  color: #060b35;
  text-align: left;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__number {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 23px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__buy {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition-duration: 1s;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition-duration: 1s;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

